import React, { Component } from "react";
import http from "./httpService.js";
class AddCar extends Component {
    state = {
        car: { id: "", price: "", year: "", kms: "", model: "", color: "" },
        edit: false,
        carmaster: [],
        colors: [],
        carmodels: []
    };

    handleChange = (e) => {
        e.preventDefault();
        const { currentTarget: input } = e;
        let s1 = { ...this.state };
        s1.car[input.name] = input.value;
        if (input.name === "model") {
            let index = s1.carmaster.findIndex(c1 => c1.model === input.value);
            if (index >= 0)
                s1.colors = s1.carmaster[index].colors;
        }
        this.setState(s1);
    }

    handleSubmit = (e) => {
        e.preventDefault();
        let { car, edit } = this.state;
        edit
            ? this.putData(`/${car.id}`, car)
            : this.postData("", car);
    }

    async putData(url, obj) {
        await http.put(url, obj);
        this.props.history.push("/cars");
    }

    async postData(url, obj) {
        await http.post(url, obj);
        this.props.history.push("/cars");
    }

    makeTextFields = (name, label, selvalue, itype, placeholder) => {
        return (
            <div>
                <label htmlFor={name} className="form-label">{label}</label>
                <input type={itype} className="form-control" name={name} value={selvalue} placeholder={placeholder} onChange={this.handleChange} disabled={this.state.edit && name === "id"} />
            </div>
        );
    }

    makeDropdown = (arr, name, selvalue, label, placeholder) => {
        return (
            <div>
                <label htmlFor={name} className="form-label">{label}</label>
                <select name={name} className="form-select" value={selvalue} onChange={this.handleChange}>
                    <option value="" disabled>{placeholder}</option>
                    {arr.map((opt, index) => (
                        <option key={index} value={opt}>{opt}</option>
                    ))}
                </select>
            </div>
        );
    }

    async fetchData() {
        const { id } = this.props.match.params;
        if (id) {
            let response1 = await http.get(`/${id}`);
            let response2 = await http.get(`/carmaster`);

            let colors = [];
            let index = response2.data.findIndex(c1 => c1.model === response1.data.model);
            if (index >= 0)
                colors = response2.data[index].colors;

            let carmodels = response2.data.map(c1 => c1.model);
            this.setState({ car: response1.data, edit: true, carmaster: response2.data, carmodels: carmodels, colors: colors });
        }
        else {
            let response = await http.get(`/carmaster`);
            let car = { id: "", price: "", year: "", kms: "", model: "", color: "" };
            let carmodels = response.data.map(c1 => c1.model);
            this.setState({ car: car, edit: false, carmaster: response.data, carmodels: carmodels, colors: [] });
        }
    }

    async componentDidMount() {
        this.fetchData();
    }

    async componentDidUpdate(prevProps, prevState) {
        if (prevProps !== this.props)
            this.fetchData();
    }

    showContent = () => {
        const { carmodels, colors, car } = this.state;
        const { id, price, year, kms } = car;
        return (
            <div>
                <div className="h4 text-center">Car Details</div>
                <div className="mb-3">
                    {this.makeTextFields("id", "Car Id", id, "text", "Enter Id")}
                </div>
                <div className="mb-3">
                    {this.makeTextFields("price", "Price", price, "number", "Enter price")}
                </div>
                <div className="mb-3">
                    {this.makeTextFields("kms", "Mileage in kms", kms, "number", "Enter mileage")}
                </div>
                <div className="mb-3">
                    {this.makeTextFields("year", "Year of Manufacture", year, "number", "Enter year")}
                </div>
                <div className="row mb-3">
                    <div className="col">
                        {this.makeDropdown(carmodels, "model", car.model, "Model", "Enter Model")}
                    </div>
                    <div className="col">
                        {this.makeDropdown(colors, "color", car.color, "Color", "Enter Color")}
                    </div>
                </div>
                <div className="text-center">
                    <button className="btn btn-primary" onClick={this.handleSubmit}>Submit</button>
                </div>

            </div>
        );
    }

    render() {
        return (
            <div className="container">
                {this.showContent()}
            </div>
        );
    }
}

export default AddCar;