import React, { Component } from "react";
class CarDetails extends Component {

    handelDeleteData = (id) => {
        this.props.onhandelDeleteData(id);
    }

    handleEditCar = (id) => {
        this.props.onhandleEditCar(id);
    }

    showContent = () => {
        const { car } = this.props;
        const { id, price, year, kms, model, color } = car;
        return (
            <div className="bg-warning text-center border">
                <div className="fw-bold">{model}</div>
                <div>Price : &#8377;{price}</div>
                <div>Color : {color}</div>
                <div>Mileage : {kms} Kms</div>
                <div>Manufactured in {year}</div>
                <div className="row">
                    <div className="col">
                        <i className="fa-regular fa-pen-to-square" style={{ fontSize: "20px", cursor: "pointer" }} onClick={() => this.handleEditCar(id)} />
                    </div>
                    <div className="col">
                        <i className="fa-solid fa-trash-can" style={{ fontSize: "20px", cursor: "pointer" }} onClick={() => this.handelDeleteData(id)} />
                    </div>
                </div>
            </div>
        );
    }

    render() {
        return this.showContent();
    }
}

export default CarDetails;