import React, { Component } from "react";
class LeftPanelOptions extends Component {
    state = {
        alloptions: { fuel: "", type: "", sortby: "" }
    };

    handleChange = (e) => {
        let options = { ...this.props.alloptions };
        const { currentTarget: input } = e;
        options[input.name] = input.value;
        this.props.onhandleOptionChange(options);
    }

    makeRadios = (arr, name, selvalue, headlabel) => {
        return (
            <div className="container border">
                <div className="fw-bold m-3" style={{ fontSize: "20px" }}>{headlabel}</div>
                <hr />
                {arr.map(opt => (
                    <div className="form-check m-2 mx-4" key={opt}>
                        <input type="radio" className="form-check-input" name={name} value={opt} checked={opt === selvalue} onChange={this.handleChange} />
                        <label htmlFor={name} className="form-check-label">{opt}</label>
                    </div>
                ))}
            </div>
        );
    }

    showContent = () => {
        const { alloptions } = this.props;
        const { fuel="", type="", sortby="" } = alloptions;
        return (
            <div>
                {this.makeRadios(["Diesel", "Petrol"], "fuel", fuel, "Fuel")}
                <br />
                {this.makeRadios(["Hatchback", "Sedan"], "type", type, "Type")}
                <br />
                {this.makeRadios(["kms", "price", "year"], "sortby", sortby, "Sort")}
            </div>
        );
    }

    render() {
        return (
            <div className="container">
                {this.showContent()}
            </div>
        );
    }
}

export default LeftPanelOptions;