import React, { Component } from "react";
import queryString from "query-string";
import http from "./httpService";
import CarDetails from "./CarDetails";
import LeftPanelOptions from "./LeftPanelOptions";
class Cars extends Component {
    state = {
        data: [],
        alloptions: {},
        pricerange: { minprice: "", maxprice: "" }
    };

    addToQueryString = (str, paramName, paramValue) => {
        return paramValue ? str ? `${str}&${paramName}=${paramValue}` : `${paramName}=${paramValue}` : str;
    }

    makeSearchString = (options) => {
        let searchStr = "";
        for (const [key, value] of Object.entries(options))
            searchStr = this.addToQueryString(searchStr, key, value);
        return searchStr;
    }

    async fetchData() {
        let queryparams = queryString.parse(this.props.location.search);
        let searchStr = this.makeSearchString(queryparams);
        if (!searchStr)
            this.setState({ alloptions: {}, pricerange: { minprice: "", maxprice: "" } });
        let response = await http.get(`?${searchStr}`);
        let { data } = response;
        this.setState({ data: data });
    }

    componentDidMount() {
        this.fetchData();
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps !== this.props)
            this.fetchData();
    }

    async deleteData(id) {
        await http.deleteApi(`/${id}`);
        this.fetchData();
    }

    handleEditCar = (id) => {
        this.props.history.push(`/cars/edit/${id}`);
    }

    handelDeleteData = (id) => {
        this.deleteData(id);
    }

    filterByPrice = (e) => {
        e.preventDefault();
        const { currentTarget: input } = e;
        let s1 = { ...this.state };
        s1.pricerange[input.name] = input.value;
        this.setState(s1);
        if (e.key === "Enter") {
            this.setState({ alloptions: {} })
            let urlstr = this.makeSearchString(this.state.pricerange);
            this.props.history.push(`/cars?${urlstr}`);
        }
    }

    handleOptionChange = (options) => {
        let queryparams = queryString.parse(this.props.location.search);
        queryparams = { ...queryparams, ...options };
        this.setState({ alloptions: options });
        let urlstr = this.makeSearchString(queryparams);
        this.props.history.push(`/cars?${urlstr}`);
    }

    showContent = () => {
        const { data } = this.state;
        return (
            <div className="row row-cols-4">
                {data.map((d1, index) => (
                    <CarDetails key={index} car={d1} onhandelDeleteData={this.handelDeleteData} onhandleEditCar={this.handleEditCar} />
                ))}
            </div>
        );
    }

    render() {
        const { pricerange } = this.state;
        return (
            <div>
                <div className="h4 text-center">All Cars</div>
                <div className="row">
                    <div className="col-3">
                        <LeftPanelOptions alloptions={this.state.alloptions} onhandleOptionChange={this.handleOptionChange} />
                    </div>
                    <div className="col">
                        <div className="row">
                            <div className="col-2 text-end" style={{ fontSize: "20px" }}>Price Range:</div>
                            <div className="col-3">
                                <input type="number" className="form-control" name="minprice" defaultValue={pricerange.minprice} placeholder="MinPrice" onKeyUp={this.filterByPrice} />
                            </div>
                            <div className="col-3">
                                <input type="number" className="form-control" name="maxprice" defaultValue={pricerange.maxprice} placeholder="MaxPrice" onKeyUp={this.filterByPrice} />
                            </div>
                            <div className="col"></div>
                        </div>
                        <div className="row my-2">
                            {this.showContent()}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Cars;